body {
    background: rgb(126, 125, 125);
    .app {
        max-width: 580px;
        height: 160px;
        padding: 50px 0;
        margin: auto;
        .headerWrapper {
            display: flex;
            justify-content: center;
            max-width: 565px;
            height: 60px;
            margin: auto;
            margin-right: 33px;
            color: white;
            background: black;
            border-radius: 20px;
            .header {
                margin-right: 5px;
                margin: auto;
                font-size: 34px;
                font-weight: bold;
            }
        }

        .content {
            display: flex;
            .pokemonList {
                max-width: 340px;
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                margin-top: 10px;
            }
            .infoWrapper {
                .typeFilter {
                    margin-left: 28px;
                    margin-top: 20px;
                    position: absolute;
                    border: 1px solid black;
                }
                .pokemonInfo {
                    margin: 202px 0 0 27px;
                }
            }
        }
    }

    @media (max-width: 1000px) {
        .app {
            .headerWrapper {
                max-width: 95%;
                margin-right: 41px;
            }
            max-width: 720px;
            .content {
                .pokemonList {
                    max-width: 420px;
                }
                .infoWrapper {
                    //width: 450px;
                    margin-left: 430px;
                    margin-top: 0;
                    position: fixed;
                    .typeFilter {
                        width: 220px;
                    }
                }
            }
        }
    }
}
